.footer {
    position: relative;
	width:100%;
	min-width: $min-width;
	order: 3;	
    -webkit-order: 3;	
    z-index: 10;
    background: #343B4C;
    letter-spacing: 0.125px;
    font-size: 14px;
    line-height: 16px; 
    color: #8D96B2;
    a {color: #8D96B2;   
        &:hover {color: #fff;}
    }
}
.footer_line {text-align: center; background: #fff; padding: 23px 0 25px; color: #343B4C;
    &_txt {font-weight: 600;font-size: 36px;line-height: 56px;font-family: $title-font-family;
        img {margin:-8px 0 0;}
    }
}

.footer_list {margin-bottom: 35px;
    li {list-style: none; line-height: 24px;
        a {color: #8D96B2;     
            &:hover {color: #fff;}
        }
    } 
}

.footer_top {padding: 18px 0; }
.footer_mid {border-top:1px solid #434c62; padding: 36px 0 40px;}
.footer_bot {border-top:1px solid #292f3c; padding: 23px 0 32px;}

.getapp {     max-width: 170px; margin-top: 3px; margin-bottom: 20px;
    &_img {width:168px;height:56px; display: flex; align-items: center; justify-content: center; background: #4A556F;border-radius: 4px; margin-bottom: 16px;font-weight: 500;font-size: 16px;line-height: 18px;
        [class^="icon-"] {font-size: 24px;margin:0 16px 0 -9px;}
        span {color: #343B4C; transition: all 0.3s linear;}
        &:hover span {color:#fff;}
    }
    &_txt {font-weight: 500;font-size: 16px;line-height: 24px;}
    a:hover span {color:#fff;}
}

.footer_contact {font-size: 16px; line-height: 24px; letter-spacing: 0.2px;    margin-bottom: 13px;
    &_item { line-height: 30px;    margin-bottom: 10px;
        [class^="icon-"] {font-size: 24px; display: inline-block; vertical-align: middle;    width: 31px;    text-align: center;    margin-right: 10px; opacity: 0.3;color:#8D96B2;}
    }
}
.goup {width: 48px;background: #EAEEFE;z-index: 20; border-radius: 4px;height: 48px; position: fixed; bottom: 40px;right:40px; display: flex;align-items: center; justify-content: center;
    span {display: inline-block; transform: rotate(-90deg);}
    &:hover {background-size: #fff;}
}

.sidehelp {position: fixed;background: url(../img/faq.png) no-repeat 0 0;padding-right: 10px;z-index: 30;margin-top:-68px; top:50%;height: 40px;width:136px;line-height: 40px;text-align: center; right:0; transform: rotate(-90deg); transform-origin: right bottom;color:#fff;font-weight: 500;letter-spacing: 0.142857px;}
.sidehelp:hover {color:#fff;}
.langsel {
    .bootstrap-select .flag-icon {    width: 24px;    height: 18px;    vertical-align: middle;    margin: -3px 4px 0 2px;}  
    .bootstrap-select>.dropdown-toggle{   font-weight: 400;
        position:relative; width:100%; z-index:1; text-align: left; white-space:nowrap; border-radius: 4px; background:#292F3C; font-size: 14px;
        color:#8D96B2; font-weight:400; height:40px; line-height: 18px; padding: 11px 30px 11px 6px;letter-spacing: 0.125px;border:none;
    }
    .bootstrap-select .dropdown-toggle .filter-option-inner {padding-right: 0;}
    .bootstrap-select .dropdown-toggle:active,  
    .bootstrap-select .dropdown-toggle:focus,
    .bootstrap-select .btn-light:not([disabled]):not(.disabled):active,
    .show > .btn-light.dropdown-toggle {
        color: #fff;
        background-color:#1c202a;
        border-color: #017cee; 
        box-shadow: 0 0 0 3px #c4e2fe; 
    }
    .bootstrap-select .dropdown-toggle::after {position: absolute;
        right: 12px; 
        top: 12px; width:12px; height:15px; border:none;     font-size: 12px; 
        content: "\e902"; color: #8D96B2;    font-family: 'icomoon' !important;
    }
    .bootstrap-select .dropdown-menu li a {color:#343B4C; padding: 11px 6px; font-size: 14px; cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}
    .bootstrap-select .dropdown-menu li a.active, .bootstrap-select .dropdown-menu li a:hover {color:#343B4C;background:#f5f8fa;}
}  
.footer_partners {margin: 0 -31px;display: flex; justify-content: center; flex-wrap: wrap; align-items: center;
    &_item {padding:15px 31px;}
    img {mix-blend-mode: overlay;opacity: 0.5;    transition: all 0.3s linear;
        &:hover {opacity: 1;}
    }
}
.footer_partners-sm {display: flex;align-items: center;justify-content: flex-start;flex-wrap: wrap;
    .footer_partners_item {padding:  0px 60px 17px 0;}
    .footer_partners_item:last-child {padding-right:0;}
    img {mix-blend-mode: screen;}  
}
.col-age {    width: 59px;    align-self: center;    padding-bottom: 12px;
    img {width: 24px;}
}

/* */
@media screen and (max-width: 1199px) {  
    .footer_partners {margin: 0 -21px;
        &_item {padding:15px 21px;}
    }
}
@media screen and (max-width: 1023px) {  
   .goup {   bottom: 10px; right: 10px;}
   .footer_partners-sm .footer_partners_item {    padding: 0px 45px 17px 0;}
}
@media screen and (max-width: 767px) {  
    .footer_copy {margin-bottom: 18px;}
    .footer_partners {margin: 0 -15px;}
    .footer_partners_item {padding: 15px 15px;}
    .footer_bot {position: relative;}
    .col-age {position: absolute;top: 84px;right: -3px;}
    .footer_partners-sm {margin-right: -35px;
        .footer_partners_item { padding: 0px 35px 17px 0;}
        .getapp_img {margin-right: 35px;}
    }
    .footer_line_txt {font-size: 30px;line-height: 44px;
        img {width:29px;}
    }
}
@media screen and (max-width: 575px) {  
    .footer {display: flex;align-items: stretch; flex-direction: column;}
    .footer_line {    padding: 24px 0 24px;}
    .footer_line_txt {font-size: 24px;line-height: 32px;
        img {width:23px;}
    }
    .footer_top {order:1;    padding: 8px 0;}
    .footer_mid {border:none; padding: 0;}
    .footer_partners {flex-wrap: nowrap;overflow-x: auto; margin:0 -8px; justify-content: flex-start;
        &_item { padding: 16px 8px; flex-shrink: 0;
            img {max-height: 24px; min-width: 14px;max-width: 72px;} 
        }
    }   
    .footer_copy {margin-bottom: 18px; max-width: 100px;}
    .footer_bot {border:none;border-bottom: 1px solid #434c62;padding: 36px 0 21px;}
    .footer_txt {line-height: 20px;}
    .langsel {position: absolute;top:32px;right:56px;}
    .col-age {top: 39px;}
}
@media screen and (max-width: 424px) {  
    
}