
/*
================================================================================
|                                     HEADER                                   |
================================================================================
*/
.header {
	width:100%; 
	order: 1;	
    -webkit-order: 1;
    position: fixed;       
    left: 0;  right:0;          
    z-index: 150;     
    top:0; width:100%;             
    height: 128px;
    background: #fff; transition: all 0.3s linear;
    &.fix {box-shadow: 0px 5px 15px rgba(52, 59, 76, 0.1);}
}   
 
.header_top { padding: 16px 0;  height: 80px;letter-spacing: 0.125px;font-size: 14px;line-height: 48px;
    [class^="icon-"] {font-size: 20px;margin-right: 10px; color:#F16142;}
}    
.logo {display: block;width:258px;height:48px; line-height: 0;}    
.header_right {display: flex;align-items: center;justify-content: center;margin-left: auto;}
.header_help {      margin-right: 22px;
    &_link {display: flex;align-items: center; }
    &_txt {margin-left: 8px;}
}
.header_basket { border-left:1px solid #EEF2F4; padding-left: 21px; margin-left: 24px;
    &_link {display: flex;align-items: center;}
    &_num {    margin-right: 13px;    margin-left: 4px;}
}
.header_user {
    &_link {margin-right: 6px;    display: inline-block;padding:0px 15px;}
    .btn {height: 40px; padding: 6px 15px;letter-spacing: 0.142857px;font-family: $base-font-family;}
}
.header_user.active {margin: 0 3px 0 4px; display: flex;align-items: center;background: rgba(49, 94, 251, 0.1);border-radius: 4px; padding: 0px 0 0 16px;}
.header_user_bal {letter-spacing: 0.125px;font-weight: 500;font-size: 14px;line-height: 16px;margin-right: 4px;}
.header_user_wr {position: relative;}
.header_user_opener {display: flex;align-items: center;color: #315EFB;height: 40px;padding: 0 16px;
    [class^="icon-"] {font-size: 18px;margin-right: 10px; color:#F16142;}
}
.header_user_not {line-height: 16px;border-radius: 8px;padding: 0 5px;min-width: 16px; background: #28BC00; position: absolute;top:-4px;right:-4px;font-weight: 500;font-size: 11px; letter-spacing: 0.0982142px;color: #FFFFFF; }
.header_user_txt {font-size: 14px;line-height: 16px;letter-spacing: 0.125px;    display: inline-block;    vertical-align: middle;    margin: 0 6px -1px 0;}

.header_bot {background: #F16142; color: #fff; height: 48px;}       
     
.header_nav.navbar {padding: 0;
    .navbar-collapse { justify-content: center; }
    .navbar-nav { justify-content: center; }
    .nav-link {color: #fff;z-index: 1010; font-weight: 500;font-size: 16px;font-family: $title-font-family; padding:10px 0px 0; height: 48px;position: relative;line-height: 30px;}
    .dropdown-toggle::after {display: none;}
    .icon-arrow_down { font-size: 11px; margin-left: 13px;}
    .nav-item {padding:0px 19.5px 0;}
    .nav-item:hover .nav-link {border-bottom: 4px solid #fff;} 
    .dropdown-menu {box-shadow:0px 10px 30px rgba(52, 59, 76, 0.2); background: transparent; border:none; border-radius: 0; padding: 8px 0 0; margin:0;
        &::before {/*content:'';*/position: absolute; top:16px;left:16px;right:16px;bottom: 0;background: rgba(52, 59, 76, 0.5);filter: blur(40.7742px);z-index: -1;}
        &_cont {background: #FFFFFF; box-shadow: inset 0px -4px 0px #F16142; padding: 16px; position: relative;z-index: 1;}
    }
    .dropdown-powerball {width:624px;left:-50px;
        .mainbanner_cont.dropdown-menu_cont {max-width:100%;border-radius: 0;background: #FFFFFF; padding: 32px;}
    }
}
.header_nav.navbar li:hover > .dropdown-menu { display: block;}

.quickgame {display: flex;align-items: center;margin:16px -16px 18px; justify-content: space-between;background: #F3F5F7;padding: 16px; border-radius: 8px;
    &_title {letter-spacing: 0.142857px; color: #8D96B2; margin-right: auto;
        [class^="icon-"] {font-size: 20px; display: inline-block; vertical-align: middle;    margin: -2px 9px 0 3px;}
    }
    &_btn {margin-left:16px; width:160px;  
        .btn {width:100%; padding: 10px;}
    }
}
.powerball_links {display: flex;align-items: center; justify-content: space-around;letter-spacing: 0.125px;font-size: 14px;
    a {display: inline-block; }
    [class^="icon-"] {font-size: 19px; display: inline-block; vertical-align: middle;    margin: -2px 9px 0 0px; color:#F16142;}
    .icon-statistic {font-size: 17px;}
}
.dropdown-tickets {left:-165px;}  
.dropdown-tickets-nacional {left: 19.5px; 
    .tickets_item {width:192px;
        &:nth-child(even) {background: #F3F5F7;}
        &:nth-child(odd) {background: #ffffff;}
        &_logo {margin: -8px 0 12px;}
    }  
}
.tickets {display: flex; align-items: stretch; justify-content: flex-start; text-align: center;
    &_item {border-radius: 8px;width:208px;padding: 16px;margin-bottom: 8px;
        &:nth-child(odd) {background: #F3F5F7;}
        &_logo {height: 76px;margin: 0px 0 12px; display: flex; align-items: center; justify-content: center;
            img {max-height: 100%; max-width: 146px;}
        }
        &_title {font-weight: 600;font-family: $title-font-family;font-size: 24px;line-height: 32px;margin:6px 0;}
        &_sm {letter-spacing: 0.125px;font-size: 14px;color: #8D96B2;    margin-bottom: 5px;}
        > .btn {display: block;margin:0 auto 8px; width:128px;}
        .time, .date {line-height: 40px; height: 40px;overflow: hidden; white-space: nowrap; width:144px;margin:0 auto 8px; padding: 0px 5px; justify-content: center;}
        .time {margin-bottom: 0;}
        .lottery_sel {margin:24px auto 16px;}
    }
}
.tickets_all {margin: 16px 0;
    .btn {width:160px;}
}
.dropdown-results {width: 792px; left: 50%; transform: translateX(-50%);
    .lastresult-list { margin-top: -13px;}
    .results_all { margin: 16px 0 8px;
        .btn {width:192px;}
    }
}

.mobile-button {display: none; position: absolute;z-index: 10; width:24px;height: 24px;
    &:before {content:'';background:#F16142; width:2px;height: 2px;margin-right:2px;}
    span {backface-visibility: hidden; border-radius:2px; position: relative; background:#F16142; transition:all 0.3s ease-out;width:14px;margin-left:4px; height:2px; display:block; margin:0px 0 0px 0px;}   
    span:before,  span:after{ transition: all 0.3s linear;backface-visibility: hidden;  content:''; background:#F16142; height:2px; display:block; width:18px; position:absolute;}
    span:before {top: 5px;left:-4px;}     
    span:after {bottom:5px;left:-4px;}     
    &.open { 
        &:before {background: transparent;}
        span {background: transparent;}       
        span:before {top:0; transform: rotate(45deg);}  
        span:after  {bottom:0px; transform: rotate(-45deg);}
    }   
}
.header_user-mob {display: none;}
.header_app {margin-bottom: 25px; font-family: $title-font-family;height: 40px; display: flex; align-items: center; position: relative;font-weight: 600;font-size: 14px;line-height: 18px;color: #343B4C;
    [class^="icon-"] {color:#28BC00; margin-right: 8px; font-size:24px;}
    &:after {width: 40px;height: 40px;content:'';position: absolute;right:0;top:0;background: url(../svg/appdwn.svg) no-repeat center #28BC00;border-radius: 4px;}
}
.header_langsel {display: flex;justify-content: flex-end;    margin: 3px -7px 11px 0;
    .bootstrap-select > .dropdown-toggle {border-color:#fff;color: #8D96B2;}
    .flag-icon {width: 24px;     margin-right: 4px;  line-height: 2em;}
}
.header_mob {display: none; position: fixed;z-index: 5; left:0;top:0;right: 0;height: 100%;     background: #fff; 
    &.open {display: block;}
    h4 {font-weight: 600; font-size: 18px; line-height: 32px; padding: 0;    margin-bottom: -2px;
        a {color: #343B4C;}
    }
    .header_user {text-align: center; border-bottom: 1px solid #EEF2F4;    padding: 10px 0 16px;    margin-bottom: 15px;}
    &_scroll {padding: 0 16px; overflow-y: auto; height: calc(100% - 62px);}
}
.header_more {
    a {letter-spacing: 0.025px;font-size: 14px;line-height: 16px;}
}

.header_mob_wrap {margin-bottom: 28px;}
.header_mob_nav {    margin-bottom: 11px;
    &:not(:last-child) {border-bottom: 1px solid #EEF2F4;}
    li {list-style: none;border-bottom: 1px solid #EEF2F4;
        &:last-child {border:none;}
        a {display: flex;    min-height: 47px; align-items: center; justify-content: space-between; padding: 11px 0;}
    }
    &_icon {width: 56px;height: 32px; display: flex;align-items: center;justify-content: center;margin: 5px 16px 5px 0;min-width: 56px;
        img {max-height: 100%;}
    }
    &_logo {height: 39px; align-items: flex-start;
        img {max-height: 27px; max-width: 55px;    margin-top: 1px;}
    }
    &_cont {flex-grow: 1;}
    &_title {display:block;font-weight: 500;font-size: 16px;line-height: 24px;color: #F16142;font-family: $title-font-family;}
    &_txt {display:block;font-size: 14px;line-height: 16px;letter-spacing: 0.125px;color: #8D96B2;}
    .icon-chevron {color:#8D96B2; min-width: 24px;text-align: center; font-size: 11px; }
}
.header-maintenance {    height: 80px;
    .logo {margin:0 auto;display: block;}
}


/* */
@media screen and (max-width: 1279px) { 
   
}   
@media screen and (max-width: 1199px) {         
    .usernav li .dropdown-menu {right:0; left:auto;}
    .header_nav.navbar .nav-item {padding: 0;}
    .header_nav.navbar .navbar-nav {justify-content: space-between;    width: 100%;}
    

}
  
@media (max-width: 991px) {  
    /*body.hid {overflow: hidden;}*/
    .header {height: 56px;}
    .header_basket_txt {display: none;}
    .header_right {margin-top: -1px;}
    .header_help_txt {display: none;}  
    .logo {position: relative;z-index: 10; width: 129px; height: 24px;margin-left:47px;}
    .mobile-button {display: flex; float: left; left:16px;top:16px; align-items: center; justify-content: center;}
    .header_top [class^="icon-"] {margin-right: 0;    font-size: 20px;}
    .header_basket {border:none; padding: 0;    margin:0 -2px 0 18px;
        &_link { height: 24px;width: 24px; position: relative;}
        &_num {position: absolute; font-size: 0; background: #28BC00;width:8px;height:8px;border-radius: 50%;    top: -4px;    right: -1px;margin:0;}
    }
    .header_user-mob {display: block;
        &_link { height: 24px;width: 24px; display: flex;align-items: center; justify-content: center;}
    }
    .header_user {
        .header_user_link {   margin-right: 27px; margin-left: 16px; font-size: 14px;font-family: "Rubrik", sans-serif; font-weight: 600;}
        .btn-outline-success {width:208px;    font-family: "Rubrik", sans-serif;}
        &.active {justify-content: space-between; background:transparent; margin:0 0 15px;    padding: 11px 0 16px;
            .header_user_link {letter-spacing: 0.125px;margin-right: 24px;    margin-left: 0px;}
        }
        .section_top_exit [class^="icon-"] {margin-right: 2px;}
    }
    .header_user_info {   text-align: left;   flex-grow: 1;}
    .header_user_title {font-size: 14px;line-height: 16px;letter-spacing: 0.125px;color: #8D96B2;  }
    .header_user_bal {font-weight: 500;font-size: 16px;line-height: 23px;color: #343B4C;font-family: "Rubrik", sans-serif;}
    .header_top {height: 56px;
        .header_user {display: none;}
    }
    .header_help {  margin-right: 18px;}
    .header-maintenance {height: 56px;}
    .bodyapp .header {height: 56px;
        .logo {margin-left:0;}
    }

}
@media (max-width: 359px) {  
    .header_user .header_user_link {    margin-right: 0px;    margin-left: 0px;}
    .logo { margin-left: 37px;}
    .header_basket {  margin: 0 -2px 0 10px;}
    .header_help {    margin-right: 13px;}
    .header_right {margin-left: -15px;}

}
