
//main styles
.main-wrapper {
    padding: 0 0 0 0;
	min-width: $min-width;
    width: 100%;
	position: relative;
	overflow: hidden;
	min-height:100%;
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex; 
	display: flex;
	flex-direction:column;
    -webkit-flex-direction:column;
    -ms-flex-direction: column;
	justify-content:flex-start;	
    -webkit-justify-content:flex-start;	
    -ms-flex-pack: start;
}  

.wrapper{
	min-width: $min-width;
	max-width: $max-width;
	padding: 0 1rem;  
	margin: 0 auto;      
	position: relative;   
	z-index: 2; 
	width: 100%; 
}  

.container {    padding-right: 16px;    padding-left: 16px;}
.row {    margin-right: -16px;    margin-left: -16px;}
.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12, .col, .col-auto, .col-sm-1, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11, .col-sm-12, .col-sm, .col-sm-auto, .col-md-1, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-10, .col-md-11, .col-md-12, .col-md, .col-md-auto, .col-lg-1, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-10, .col-lg-11, .col-lg-12, .col-lg, .col-lg-auto, .col-xl-1, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, .col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-10, .col-xl-11, .col-xl-12, .col-xl, .col-xl-auto {
    padding-right: 16px;
    padding-left: 16px;
}

/* titles */    
p{	
	padding: 14px 0 14px 0; padding: 0px 0 25px 0;
}
h1, .h1{	transition: all 0.3s;
	@include font($font-size-h1,$line-height-h1,$title-color);
	padding: 10px 0 10px 0px;   
	font-weight: 600;	
	font-family: $title-font-family;
	margin-bottom: 0px;
}
h2, .h2{
	@include font($font-size-h2,$line-height-h2,$title-color);
	padding: 10px 0 10px 0;       
	font-weight: 600;	
	font-family: $title-font-family;
	margin-bottom: 0px;
}    
h3, .h3{
	@include font($font-size-h3,$line-height-h3,$title-color);
	padding: 10px 0 10px 0; 
	font-weight: 600;	
	font-family: $title-font-family;	
	margin-bottom: 0px;    
}
h4, .h4{
	@include font($font-size-h4,$line-height-h4,inherit);
	padding: 10px 0 10px 0px; 
	position: relative;    
	font-weight: bold;	  
	font-family: $title-font-family;
	letter-spacing: 0;
}
h5, .h5{
	@include font($font-size-h5,$line-height-h5,inherit);
	padding: 10px 0 10px 0px; 
	position: relative;    
	font-weight: bold;	  
	font-family: $title-font-family;
}
h6, .h6{
	@include font($font-size-h6,$line-height-h6,inherit);
	padding: 10px 0 10px 0px; 
	position: relative;    
	font-weight: bold;	  
	font-family: $title-font-family;
}

/* text position */
.text-left{text-align: left!important}
.text-center{text-align: center!important}   
.text-right{text-align: right!important}
.nowrap{white-space: nowrap!important;}
   
/* loader */
.loaded .main-wrapper{visibility:hidden;}     
.icon-load{position:fixed;width:100%;height:100%; top:0;left:0;display:none; background-color:#fff;z-index: 200;}
.loaded .icon-load{display:block;}

.c-gray {color:$gray;}

/*
================================================================================
|                                     CONTENT                                 |
================================================================================
*/
.content{	
	min-width: $min-width;
	text-align: left;
    width: 100%;  
	order: 2;  
    -webkit-order: 2;
	flex-grow: 1;
    -webkit-flex-grow: 1;  
    padding-top: 128px;
}         


/* */
.btn {font-size: 16px;font-weight: 600;height: 48px;line-height: 26px;padding: 10px 30px; border-radius: 4px; font-family: $title-font-family;letter-spacing: 0.177778px;}
.btn-sm {border-radius: 14px; font-size: 14px; font-weight: bold; padding: 0.15rem 0.9rem;}
.btn-primary {background: #315EFB;border-color:#315EFB;
	&:hover {background: #2450ed;border-color:#2450ed;}
}   
.btn-outline-primary {border-color:#315EFB; border-width: 1.5px; color: #315EFB;    line-height: 24px;
	&:hover {}
} 
.btn-secondary {background: #b8b8b8; border-color:#b8b8b8;}      
.btn-outline-secondary {padding: 0.79rem 2.08rem;color: #989898; border-color: #989898; letter-spacing: 1px;
	&:hover {color:#000; border-color:#000; background: transparent;}  
}    
.btn-success {color: #fff;    background-color: #28BC00;    border-color: #28BC00;
	&:hover {background: #1C8400;border-color:#1C8400;}
} 
.btn-outline-success {border-color:$green; color:$green;
	&:hover {background: #1C8400;border-color:#1C8400;}
}
       
.btn-outline-primary:not([disabled]):not(.disabled):active,   
.btn-outline-primary:not([disabled]):not(.disabled):focus,
.btn-outline-secondary:not([disabled]):not(.disabled):active,
.btn-outline-secondary:not([disabled]):not(.disabled):focus  {color:#000; border-color:#000; background: transparent;}

.form-control {border: 1px solid #C6CAD8;height: 48px; border-radius: 4px; padding: 13px 15px;
font-size: 16px;  font-weight: 500;letter-spacing: 0.4px;color: #343B4C;}
.form-control:focus {color: #343B4C; border-color: #C6CAD8; box-shadow: 0 0 0 0px transparent;}   
input[type="text"], input[type="email"],input[type="password"],input[type="tel"] {white-space: nowrap; text-overflow: ellipsis;overflow: hidden;}
.form-control::-webkit-input-placeholder { font-size: 14px;color: #8D96B2; font-weight: 400; letter-spacing: 0.125px;}
.form-control:-moz-placeholder { font-size: 14px;color: #8D96B2; font-weight: 400; letter-spacing: 0.125px; }
.form-control::-moz-placeholder { font-size: 14px;color: #8D96B2; font-weight: 400; letter-spacing: 0.125px; }
.form-control:-ms-input-placeholder { font-size: 14px;color: #8D96B2; font-weight: 400; letter-spacing: 0.125px; }

.form-submit { 
	&.text-right {
		.btn { margin-left: 37px;}
	}
	.btn {min-width: 153px;}
}
.form-clear_link {font-size: 14px;}
.form_title {font-weight: bold; line-height: 1.3;    padding-top: 3px;    padding-bottom: 2px;   margin-bottom: 17px;}

.custom-select {border-radius: 4px; border:1px solid #ced4da; padding-left: 15px;}

.custom-file {  position: relative;  display: inline-block;  width: 100%;  height: calc(2.25rem + 2px);  margin-bottom: 0;}
.custom-file-input {  position: relative;  z-index: 2;  width: 100%;  height: calc(2.25rem + 2px);  margin: 0;  opacity: 0;}
.custom-file-input:focus ~ .custom-file-label {  border-color: #80bdff;  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);}
.custom-file-input:focus ~ .custom-file-label::after {  border-color: #80bdff;}
.custom-file-input:disabled ~ .custom-file-label {  background-color: #e9ecef;}
.custom-file-input:lang(en) ~ .custom-file-label::after {  content: "Browse";}
.custom-file-label {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
  height: calc(2.25rem + 2px);
  padding: 0.375rem 0.75rem;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
}

.custom-file-label::after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 3;
  display: block;
  height: 2.25rem;
  padding: 0.375rem 0.75rem;
  line-height: 1.5;
  color: #495057;
  content: "Browse";
  background-color: #e9ecef;
  border-left: 1px solid #ced4da;
  border-radius: 0 0.25rem 0.25rem 0;
}

